<template><section><h1>UrlTemplateImageryProvider</h1>
<p><code v-pre>vc-provider-imagery-urltemplate</code> 组件通过一个约定的 URL 模板来请求影像图层。比如加载国内的高德，腾讯等影像服务，URL 都是一个固定的规范，都可以通过该组件轻松实现。</p>
<h2>示例</h2>
<h3>加载 UrlTemplate 影像服务图层</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-layer-imagery :alpha="alpha" ref="layerText" :brightness="brightness" :contrast="contrast" :sortOrder="20">
          <vc-provider-imagery-urltemplate :url="urlText"></vc-provider-imagery-urltemplate>
        </vc-layer-imagery>
        <vc-layer-imagery :alpha="alpha" :brightness="brightness" :contrast="contrast" :sortOrder="10">
          <vc-provider-imagery-urltemplate :url="url"></vc-provider-imagery-urltemplate>
        </vc-layer-imagery>
      </vc-viewer>
      <div class="demo-tool">
        <span>透明度</span>
        <vue-slider v-model="alpha" :min="0" :max="1" :interval="0.01"></vue-slider>
        <span>亮度</span>
        <vue-slider v-model="brightness" :min="0" :max="3" :interval="0.01"></vue-slider>
        <span>对比度</span>
        <vue-slider v-model="contrast" :min="0" :max="3" :interval="0.01"></vue-slider>
        <span>切换服务</span>
        <md-select v-model="url" placeholder="请选择服务">
          <md-option v-for="item in options" :key="item.value" :value="item.value">
            {{item.label}}
          </md-option>
        </md-select>
      </div>
    </div>
  </template>
  
</doc-preview>
