<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String|Object</td>
<td></td>
<td><code v-pre>required</code>指定服务地址。</td>
</tr>
<tr>
<td>pickFeaturesUrl</td>
<td>String|Object</td>
<td></td>
<td><code v-pre>optional</code>指定拾取对象属性的 url，如果无效，会返回 undefined。</td>
</tr>
<tr>
<td>urlSchemeZeroPadding</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code>Gets the URL scheme zero padding for each tile coordinate.</td>
</tr>
<tr>
<td>subdomains</td>
<td>String</td>
<td><code v-pre>'abc'</code></td>
<td><code v-pre>optional</code>he subdomains to use for the {s} placeholder in the URL template. If this parameter is a single string, each character in the string is a subdomain. If it is an array, each element in the array is a subdomain.</td>
</tr>
<tr>
<td>credit</td>
<td>String</td>
<td><code v-pre>''</code></td>
<td><code v-pre>optional</code>指定服务的描述信息</td>
</tr>
<tr>
<td>minimumLevel</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code>最小层级。</td>
</tr>
<tr>
<td>maximumLevel</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code>最大层级。</td>
</tr>
<tr>
<td>rectangle</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code>图层的矩形范围,此矩形限制了影像可见范围。 <strong>结构：{ west: number, south: number, east: number, north: number }</strong></td>
</tr>
<tr>
<td>tilingScheme</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code>The tiling scheme specifying how the ellipsoidal surface is broken into tiles. If this parameter is not provided, a WebMercatorTilingScheme is used.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code>参考椭球体。</td>
</tr>
<tr>
<td>tileWidth</td>
<td>Number</td>
<td><code v-pre>256</code></td>
<td><code v-pre>optional</code>像元宽度。</td>
</tr>
<tr>
<td>tileHeight</td>
<td>Number</td>
<td><code v-pre>256</code></td>
<td><code v-pre>optional</code>像元高度。</td>
</tr>
<tr>
<td>hasAlphaChannel</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code>设置为 true 表示图层包含 alpha 透明通道，反之没有。</td>
</tr>
<tr>
<td>getFeatureInfoFormats</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code>格式化拾取对象属性时提示信息位置，该项要设置 pickFeaturesUrl 且起作用时才起作用。</td>
</tr>
<tr>
<td>enablePickFeatures</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code>是否开启图层拾取。</td>
</tr>
<tr>
<td>customTags</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code>替换 url 模板中的自定义关键字。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/UrlTemplateImageryProvider.html">UrlTemplateImageryProvider</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>当图层提供者发生异步错误时触发, 返回一个 TileProviderError 实例。</td>
</tr>
<tr>
<td>readyPromise</td>
<td>ImageryProvider</td>
<td>当图层提供者准备好时触发。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          url: 'https://webst01.is.autonavi.com/appmaptile?style=7&x={x}&y={y}&z={z}',
          urlText: 'https://wprd04.is.autonavi.com/appmaptile?x={x}&y={y}&z={z}&lang=zh_cn&size=1&scl=1&style=8&ltype=12',
          options: [
            {
              value: 'https://webst01.is.autonavi.com/appmaptile?style=6&x={x}&y={y}&z={z}',
              label: '高德影像地图服务'
            },
            {
              value: 'https://mt1.google.cn/vt/lyrs=s&hl=zh-CN&x={x}&y={y}&z={z}&s=Gali',
              label: '谷歌影像地图服务'
            },
            {
              value: 'https://webst01.is.autonavi.com/appmaptile?style=7&x={x}&y={y}&z={z}',
              label: '高德矢量地图服务'
            }
          ],
          alpha: 1,
          brightness: 1,
          contrast: 1
        }
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          this.cesiumInstance = cesiumInstance
        }
      }
    }
  </script>